.listLeagues {
    display: grid;
    grid-template: 1fr/1.5fr 1.5fr 3fr 0.5fr 0.5fr;
    grid-column-gap: 5%;
}

.listLeagues div {
    justify-self: center;
    align-self: center;
}

.list {
    display: grid;
    justify-content: center;
    grid-gap: 1%;
    grid-template-columns: 70%;
}

.img {
    width: 150px;
    height: 150px;
}

.hr {
    margin-top: 2%;
    width: 100%;
    height: 2px;
    border: solid 2px royalblue;
}


@media screen and (max-width: 860px) {
    .listLeagues {
        grid-template: 1fr 1fr/1.5fr 1.5fr 3fr;
    }

    .calendar {
        grid-column-start: 3;
        grid-column-end: 4;
    }
}

@media screen and (max-width: 500px) {
    .list {
        grid-template-columns: 100%;
        justify-content: start;
        margin-left: 5%;
    }

    .img {
        width: 80px;
        height: 80px;
    }

    form .search {
        margin: 2%;
        flex-direction: column;
    }

    form .search input {
        margin: 1%;
    }

    form {
        flex-direction: column;
        margin: 1%;
    }
}

