div {
    font-size: 18px;
}

a {
    text-decoration: none;
    color: #0f7afc;
    border-bottom: 1px solid;
    border-bottom-color: rgba(15, 122, 252, 0.2);
}

a:hover {
    text-decoration: none;
    color: rgb(48, 176, 172);
    border-bottom-color: rgb(121, 84, 218);
}


@media screen and (max-width: 700px) {
    div {
        font-size: 16px;
    }
}

